	import {comingSoon,newMovies,top250} from '../../apis'
	export default {
		data() {
			return {
				moviesList: []
			}
		},
		onReady() {
			this.$store.dispatch('getCity');
			this.pageInit();
			
		},
		computed: {
			nowPlayingList() {
				return this.$store.state.nowPlayingList;
			}
		},
		methods: {
			pageInit() {
				uni.showLoading({
					title: '加载中'
				})
                Promise.all([
					this._comingSoon(),
					this._newMovies(),
					this._top250()
				]).then((res)=>{
					uni.hideLoading()
					// console.log(res)
					let rel = []
					res.map((item)=>{
					 rel.push(item[1].data);
					})
					console.log(rel)
					this.moviesList = rel;
					
				})
			},
			_comingSoon(){
				return  comingSoon({
						city: this.$store.state.city,
						start: "0",
						count: '8'
					})
			},
            _newMovies(){
				return newMovies({
					city: this.$store.state.city,
					start: "0",
					count: '8'
				})
			},
			_top250(){
				return top250({
					city: this.$store.state.city,
					start: "0",
					count: '8'
				})
			},
			toDtails(id){
				console.log(id)
				uni.navigateTo({
					url: '../details/details?id=' + id,
				})
			}
		},
	}
